.rightNav{
    position:relative;
    width: 60%;
    text-align:right;
    display: -webkit-flex;
    display: flex;

    -webkit-flex-direction: row;

            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    font-family:sans-serif;
