/* ==================== Logos */
.gh, .li, .insta, .contact, .resume{
    height:50px;
    width:50px;
    position:relative;
    display: inline-block;
    margin: 0.5rem;
}
.gh IMG, .li IMG, .insta IMG, .contact IMG, .resume IMG{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .35s ;
}

  .gh2:hover, .li2:hover, .insta2:hover, .contact2:hover, .resume2:hover {
    opacity:0;
 }
 
 .gh2, .li2, .insta2, .contact2, .resume2 {
    opacity:1;
 }
 
 /* modal */

 .modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition:  linear 0.25s, opacity 0.25s 0s, -webkit-transform 0.25s;
    transition:  linear 0.25s, opacity 0.25s 0s, transform 0.25s;
    transition:  linear 0.25s, opacity 0.25s 0s, transform 0.25s, -webkit-transform 0.25s;
    z-index: 10;
}

.modalPosition{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.modalContact {
    background-color: white;
    padding:0.75rem;
    padding-top:1.5rem;
    width: 20rem;
    border-radius: 0.5rem;
    text-align: center;
    box-shadow: 2px 2px 3px rgb(0,0,0,0.5)
}
.closeButton {
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color:  white;
    width: 1.5rem;
    line-height:1.5rem;
    height:1.5rem;
    text-align: center;
    font-weight: 800;
    cursor: pointer;
    border-radius: 0.25rem;
    background-color:rgb(50,50,50);
    transition: 0.25s
}
.closeButton:hover {
    /* color: rgb(35,64,143); */
    background-color:transparent;
}

.glyphs{
    color:  rgb(0,183,241);
